.movies .movie {
    background-color: #fff;
    margin-bottom: 70px;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #adaeb9;
    box-shadow: 0 13px 37px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3),0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    /* box-shadow : x축y축 blur spread color ; spread는 그림자 확장, 양수는 확장, 음수는 축소 */
}
.movies .movie a {
    display: grid;
    grid-template-columns: minmax(150px, 1fr) 2fr;
    grid-gap: 20px;
    text-decoration: none;
    color: inherit;
}

.movie img {
    position: relative;
    top: -50px;
    max-width: 150px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25), 0 18px 36px -18px rgba(0, 0, 0, 0.3),0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .movie_title,
.movie .movie_year {
    margin: 0;
    font-weight: 300;
}

.movie .movie_title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #3b7ffc;
    font-weight: bold;
}

.movie .movie_genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 5px 0px;
}

.movie_genre,
.movie_year {
    margin-right: 10px;
    font-size: 14px;
}

.movies {
    display: grid;
    grid-template-columns: repeat(2, minmax(400px, 1fr));
    grid-gap: 100px;
    padding: 50px;
    width: 80%;
    padding-top: 70px;
}
@media screen and(max-width: 767px) { /*0~767*/
    .movies{
        grid-template-columns: 1fr;
        width: 100%;
    }
}



